.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-white);
}
.blogs {
  margin: 0;
  width: 161px;
  height: 63px;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  flex-shrink: 0;
  z-index: 1;
}
.blog-card {
  width: 656px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: var(--gap-37xl);
  max-width: 100%;
  z-index: 1;
}
.blogs-container,
.hero {
  align-self: stretch;
  box-sizing: border-box;
  max-width: 100%;
}
.blogs-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 var(--padding-48xl);
  margin-top: -63px;
}
.hero {
  padding: var(--padding-47xl) var(--padding-90xl) var(--padding-400xl);
  position: relative;
  flex-shrink: 0;
}
.hero,
.hero-parent,
.webpage {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.hero-parent {
  align-self: stretch;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-37xl);
  color: var(--color-gray-300);
  font-family: var(--font-inter);
}
.webpage {
  width: 100%;
  position: relative;
  background-color: var(--color-gray-100);
  overflow: hidden;
  padding: 0 0 var(--padding-43xl);
  box-sizing: border-box;
  gap: var(--gap-lgi);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .hero {
    padding-top: var(--padding-24xl);
    padding-bottom: var(--padding-253xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 825px) {
  .blogs {
    font-size: var(--font-size-26xl);
  }
  .blog-card {
    gap: var(--gap-9xl);
  }
  .blogs-container,
  .hero {
    padding-left: var(--padding-14xl);
    padding-right: var(--padding-14xl);
    box-sizing: border-box;
  }
  .hero {
    padding-left: var(--padding-35xl);
    padding-right: var(--padding-35xl);
  }
}
@media screen and (max-width: 450px) {
  .blogs {
    font-size: var(--font-size-15xl);
  }
  .hero {
    padding: var(--padding-9xl) var(--padding-xl) var(--padding-158xl);
    box-sizing: border-box;
  }
}
